body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

@font-face{
    font-family:'BebasNeueRegular';
    src:url(/folio/static/media/BebasNeueRegular.9042a37c.ttf);
    src:local('BebasNenueRegular'),
        url(/folio/static/media/BebasNeueRegular.323774fa.eot) format('embedded-opentype'),
        url(/folio/static/media/BebasNeueRegular.508adc52.woff) format('woff'),
        url(/folio/static/media/BebasNeueRegular.9042a37c.ttf) format('truetype');
    font-weight:400;
    font-style:normal;
}
@font-face{
    font-family:'OpenSansRegular';
    src:url(/folio/static/media/OpenSans-Regular.629a55a7.ttf);
    src:local('OpenSansRegular'),
        url(/folio/static/media/OpenSans.5cca0e3c.eot) format('embedded-opentype'),
        url(/folio/static/media/OpenSans.c426bf8b.woff) format('woff'),
        url(/folio/static/media/OpenSans-Regular.629a55a7.ttf) format('truetype');
    font-weight:400;
    font-style:normal;
}
@font-face{
    font-family:'OpenSans LightItaliac';
    src:url(/folio/static/media/OpenSans-LightItalic.6943fb6f.ttf);
    src:local('OpenSans LightItaliac'),
        url(/folio/static/media/OpenSansLight-Italic.7ad32007.eot) format('embedded-opentype'),
        url(/folio/static/media/OpenSansLight-Italic.99a562fc.woff) format('woff'),
        url(/folio/static/media/OpenSans-LightItalic.6943fb6f.ttf) format('truetype');
    font-weight:400;
    font-style:normal;
}
@font-face{
    font-family:'OpenSans Semibold';
    src:url(/folio/static/media/OpenSans-Semibold.33f225b8.ttf);
    src:local('OpenSans Semibold'),
        url(/folio/static/media/OpenSans-Semibold.33f225b8.ttf) format('embedded-opentype'),
        url(/folio/static/media/OpenSans-Semibold.6cfaf3d6.woff) format('woff'),
        url(/folio/static/media/OpenSans-Semibold.33f225b8.ttf) format('truetype');
    font-weight:400;
    font-style:normal;
}
@font-face{
    font-family:'OpenSans SemiboldItaliac';
    src:url(/folio/static/media/OpenSans-SemiboldItalic.73f7301a.ttf);
    src:local('OpenSans SemiboldItalic'),
        url(/folio/static/media/OpenSans-SemiboldItalic.73f7301a.ttf) format('embedded-opentype'),
        url(/folio/static/media/OpenSans-SemiboldItalic.16321bf3.woff) format('woff'),
        url(/folio/static/media/OpenSans-SemiboldItalic.73f7301a.ttf) format('truetype');
    font-weight:400;
    font-style:normal;
}
.nav {
    max-width: 1300px;
    padding-right: 16px !important;
}

.navbar-brand {
    max-width: 90px;
    font-family:'BebasNeueRegular';
}

.navbar-brand h3 {
    font-size: 36px;
}

.login {
    display: grid;
    grid-template-rows: repeat(2, 1fr);
    font-size: 15px;
}

.login a {
    padding: 0;
}
@media (max-width: 576px) {
    .header {
        background-position-x: -450px !important;
    }
}
.header {
    background-color: rgb(54, 54, 54);
    background: url(/folio/static/media/photo_head.5396b42d.jpg) no-repeat center;
    height: 400px;
    max-width: 1300px;
    background-image: -webkit-image-set(
        url(/folio/static/media/photo_head.5396b42d.jpg) 1x,
        url(/folio/static/media/photo_head-2x.984f01e9.jpg) 2x
    );
    background-image: image-set(
        url(/folio/static/media/photo_head.5396b42d.jpg) 1x,
        url(/folio/static/media/photo_head-2x.984f01e9.jpg) 2x
    );
}

.tag {
    color: white;
    font-family:'BebasNeueRegular';
    max-width: 800px;
}

.tag h1 {
    font-size: 68px;
    margin: 0;
    margin-bottom: -18px;
    max-width: 200px;
}

.tag h3 {
    max-width: 200px;
    margin: 0;
}
.footer {
    max-width: 1300px;
    background-color: rgb(54, 54, 54);
    background: url(/folio/static/media/footer.474d6b05.jpg) no-repeat center;
    height: 100px;
    display: grid;
    background-image: -webkit-image-set(
        url(/folio/static/media/footer.474d6b05.jpg) 1x,
        url(/folio/static/media/footer-2x.844129ff.jpg) 2x
    );
    background-image: image-set(
        url(/folio/static/media/footer.474d6b05.jpg) 1x,
        url(/folio/static/media/footer-2x.844129ff.jpg) 2x
    );
}
.headline {
    font-size: 36px;
    font-family: 'BebasNeueRegular';
}
.about-image {
    width: 280px;
    height: 100%;
}

.about {
    width:100%;
    max-width: 1000px;
}
.main-content {
    width: 100%;
    max-width: 1300px;
    background-color: #D8D8D8;
    margin: auto;
    padding-top: 45px;
    padding-bottom: 45px;
}
.form-box {
    width: 100%;
    max-width: 580px;
    margin: auto;
    padding: 5px;
}
.fade-enter {
  opacity: 0;
  z-index: 1;
}

.fade-enter.fade-enter-active {
  opacity: 1;
  transition: opacity 250ms ease-in;
}

.fade-exit.fade-exit-active {
  display: none;
}
